'use client'

import { useState } from 'react'
import { motion } from 'framer-motion'
import { Instagram, ArrowRight, Shield, Lock, CheckCircle } from 'lucide-react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Navbar } from '@/components/ui/navbar'
import { useRouter, useParams } from 'next/navigation'

// Custom TikTok Icon
const TikTokIcon = ({ className }: { className?: string }) => (
  <svg 
    viewBox="0 0 24 24" 
    fill="currentColor" 
    className={className}
    xmlns="http://www.w3.org/2000/svg"
  >
    <path d="M19.589 6.686a4.793 4.793 0 0 1-3.77-4.245V2h-3.445v13.672a2.896 2.896 0 0 1-5.201 1.743l-.002-.001.002.001a2.895 2.895 0 0 1 3.183-4.51v-3.5a6.329 6.329 0 0 0-5.394 10.692 6.33 6.33 0 0 0 10.857-4.424V8.687a8.182 8.182 0 0 0 4.773 1.526V6.79a4.831 4.831 0 0 1-1.003-.104z"/>
  </svg>
)

export default function SetupPage() {
  const params = useParams()
  const platform = typeof params?.platform === 'string' ? params.platform : 'instagram'
  const router = useRouter()
  const [loading, setLoading] = useState(false)
  
  const isInstagram = platform === 'instagram'
  const PlatformIcon = isInstagram ? Instagram : TikTokIcon
  const platformName = isInstagram ? 'Instagram' : 'TikTok'
  const gradientClass = isInstagram ? 'from-purple-500 to-pink-500' : 'from-cyan-500 to-blue-500'

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    // Simulate API call
    setTimeout(() => {
      setLoading(false)
      // Navigate to dashboard or next step
      alert("Setup complete! Redirecting to dashboard...")
    }, 2000)
  }

  return (
    <div className="min-h-screen bg-background flex flex-col">
      <Navbar />
      
      <div className="flex-1 flex items-center justify-center p-4 pt-20">
        <div className="max-w-md w-full">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="bg-card border border-white/10 rounded-3xl p-8 shadow-2xl"
          >
            <div className="text-center mb-8">
              <div className={`w-16 h-16 mx-auto rounded-2xl bg-gradient-to-br ${gradientClass} flex items-center justify-center mb-4 shadow-lg`}>
                <PlatformIcon className="w-8 h-8 text-white" />
              </div>
              <h1 className="text-2xl font-bold mb-2">Connect your {platformName}</h1>
              <p className="text-muted-foreground text-sm">
                Enter your details to start your 7-day free trial.
              </p>
            </div>

            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="space-y-2">
                <label className="text-sm font-medium ml-1">Username</label>
                <div className="relative">
                  <div className="absolute left-4 top-1/2 -translate-y-1/2 text-muted-foreground">@</div>
                  <Input 
                    placeholder={`${platformName.toLowerCase()}_username`}
                    className="pl-8 h-12 bg-white/5 border-white/10 focus:border-primary rounded-xl"
                    required
                  />
                </div>
              </div>

              <div className="space-y-2">
                <label className="text-sm font-medium ml-1">Email Address</label>
                <Input 
                  type="email"
                  placeholder="you@example.com"
                  className="h-12 bg-white/5 border-white/10 focus:border-primary rounded-xl"
                  required
                />
              </div>

              <div className="bg-blue-500/10 border border-blue-500/20 rounded-xl p-4 flex gap-3">
                <Shield className="w-5 h-5 text-blue-400 shrink-0" />
                <p className="text-xs text-blue-200 leading-relaxed">
                  <span className="font-bold block mb-1">Security Guaranteed</span>
                  We never ask for your social media password. Verification is secure and token-based only.
                </p>
              </div>

              <Button 
                type="submit" 
                className={`w-full h-12 text-lg rounded-xl bg-gradient-to-r ${gradientClass} hover:opacity-90 border-0`}
                disabled={loading}
              >
                {loading ? (
                  <span className="flex items-center gap-2">
                    <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                    Verifying...
                  </span>
                ) : (
                  <span className="flex items-center gap-2">
                    Activate My Trial <ArrowRight className="w-4 h-4" />
                  </span>
                )}
              </Button>
            </form>

            <div className="mt-6 pt-6 border-t border-white/5 flex justify-center gap-6">
              <div className="flex items-center gap-1.5 text-xs text-muted-foreground">
                <Lock className="w-3 h-3" /> SSL Secure
              </div>
              <div className="flex items-center gap-1.5 text-xs text-muted-foreground">
                <CheckCircle className="w-3 h-3" /> No Credit Card
              </div>
            </div>
          </motion.div>
        </div>
      </div>
    </div>
  )
}
